.components-tab-panel {
  font-weight: 400;
  font-size: 13px;
  line-height: normal;
  transition: box-shadow 0.1s linear;
}
.components-tab-panel__tabs {
  position: relative;
}
.components-tab-panel__border {
  width: 100%;
  height: 1px;
  background-color: #ddd;
  position: absolute;
  bottom: 0;
  z-index: 0;
}
.popup_trigger_condition {
  position: relative;
  display: flex;
  align-items: stretch;
  margin: 0.5rem 0;
  gap: 0.25rem;
}
.popup_trigger_condition:hover .popup_condition_remove {
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup_condition_wrapper {
  display: flex;
  align-items: stretch;
  width: 90%;
  gap: 0.25rem;
}
.popup_condition_button,
.popup_trigger_condition select,
.popup_trigger_condition input,
.popup_trigger_condition p {
  font-size: 13px;
  display: flex;
  align-items: center;
  width: calc(100% / 3);
  padding-left: 12px;
  padding-right: 12px;
  border: 1px solid #949494;
  border-radius: 2px;
  box-shadow: 0 0 0 #0000;
  margin: 0;
  width: 100%;
}
.popup_condition_button.hidden,
.popup_trigger_condition select.hidden,
.popup_trigger_condition input.hidden,
.popup_trigger_condition p.hidden {
  display: none;
}
.popup_trigger_group {
  display: flex;
  align-items: center;
  width: calc(100% / 3);
}
.popup_trigger_group select,
.popup_trigger_group input,
.popup_trigger_group p {
  width: 100%;
}
.popup_condition_button {
  width: 10%;
  height: auto;
  justify-content: center;
  cursor: pointer;
  min-width: 32px;
  padding: 4px;
  background: var(--wp-components-color-accent, var(--wp-admin-theme-color, #3858e9));
  color: var(--wp-components-color-accent-inverted, #fff);
  outline: 1px solid #0000;
  text-decoration: none;
  text-shadow: none;
  white-space: nowrap;
  border: 0;
}
.popup_condition_button:hover {
  color: var(--wp-components-color-accent, var(--wp-admin-theme-color, #3858e9));
  background-color: white;
  border: 1px solid;
}
.popup_condition_button.or-button {
  width: fit-content;
  padding: 6px 12px;
}
.popup_condition_remove {
  position: absolute;
  top: 2.5px;
  right: calc(25px / -2);
  width: 25px;
  height: 25px;
  color: var(--wp-components-color-accent, var(--wp-admin-theme-color, #3858e9));
  border: 1px solid;
  border-radius: 100px;
  background-color: #fff;
  cursor: pointer;
  transform-origin: center;
  display: none;
}
.popup_condition_remove:hover {
  color: #dc143c;
  background-color: #ffe4e9;
}
.temp-options {
  margin: 12px 0;
}
